<template>
  <div>
    <el-table
      :data="tableData"
      style="width: 100%"
    >
      <el-table-column
        v-for="(item) in column"
        :key="Math.random() + item.prop"
        :label="item.label"
        width="100px"
      >
        <template slot-scope="scope">
          <span :class="{current: scope.row.current === scope.row[item.prop]}">{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import levelList from '../../../js-test/level.json';
import axios from 'axios';
import _ from 'lodash';
export default {
  name: 'VueTest',
  data () {
    return {
      tableData: [],
      column: []
    };
  },
  mounted () {
    this.init();
    setInterval(this.init, 1000 * 5);
  },
  methods: {
    async init () {
      let data = await axios.get('/v5/stock/realtime/quotec.json?symbol=SH601225,SH600438,SZ000858,SH600089,SH600887,SH601318,SZ000333,SH600277,SH510310,SH600036,SH601899,SH600585,SH600018,SH600900,SH600031&_=1667538020157')
        .then((res)=>{
          return (res.data.data);
        })
        .catch((e)=>{console.error(e);});
      for(let dataItem of data){
        dataItem.symbol = dataItem.symbol.replace('SZ', '');
        dataItem.symbol = dataItem.symbol.replace('SH', '');
      }
      if(_.isEmpty(this.column)){
        this.column.push({
          label: '名称',
          prop: 'name'
        });
        for(let i = 0; i < levelList[0].list.length; i++){
          this.column.push({
            label: -90 + 15 * i + '%',
            prop: 'p' + 2 * i
          });
          this.column.push({
            label: '-',
            prop: 'p' + (2 * i + 1)
          });
        }
      }

      for(let levelItem of levelList){
        let currentData = _.find(data, {symbol: levelItem.code});
        if(!currentData)continue;

        let setCurrentEnd = false;
        for(let i = 0; i < levelItem.list.length; i++){
          let level = levelItem.list[i];
          levelItem['p' + 2 * i] = level;
          if(currentData.current < levelItem.list[i + 1] && !setCurrentEnd){
            setCurrentEnd = true;
            levelItem['p' + (2 * i + 1)] = currentData.current;
          }else{
            levelItem['p' + (2 * i + 1)] = '';
          }
        }
      }
      this.tableData = levelList;
    },
    getLabel(item, key){
      if(key % 2 === 1){
        return (Math.ceil(item / 2) - 7 ) * 15 + '%';
      }else{
        return '-';
      }
    }
  }
};
</script>

<style scoped lang="less">
.current{
  color: #0000ff;
}
</style>

